.profile__avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 26px;
  object-position: center;
  z-index: 1;
  pointer-events: none;
  background-size: cover;
}

@media screen and (max-width: 753px) {
  .profile__avatar {
    margin-right: 0;
  }
}
